.gallery_main-page {
  display: grid;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;

  grid-template-columns: repeat(1, 280px);
  grid-auto-rows: 443px;

  grid-row-gap: 20px;
  justify-content: center;

  list-style: none;

  @include tablet {
    grid-template-columns: repeat(2, 336px);
    grid-auto-rows: 494px;
    grid-column-gap: 32px;
    grid-row-gap: 32px;
    padding-top: 60px;
    padding-bottom: 60px;
    margin-left: auto;
    margin-right: auto;
  }

  @include desktop {
    grid-template-columns: repeat(3, 395px);
    grid-auto-rows: 634px;
    grid-column-gap: 16px;
    grid-row-gap: 32px;
    justify-content: center;
    list-style: none;
  }
}

