.gallery__item-descr-wrap {
  margin-top: 10px;

  @include tablet {
    margin-top: 8px;
  }

  @include desktop {
    margin-top: 12px;
  }
}

.gallery__item-title {
  font-family: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  text-transform: uppercase;
  line-height: 1.16;

  @include tablet {
    font-size: 12px;
    line-height: 1.2;
  }

  @include desktop {
    font-size: 20px;
    line-height: 1.2;
  }
}

.gallery__item-title--isDarkTheme {
  font-family: $font-primary;
  // font-style: normal;
  font-weight: 500;
  color: $light-color-primary;
  font-size: 12px;
 
  line-height: 1.16;

  @include tablet {
    font-size: 12px;
    line-height: 1.2;
  }

  @include desktop {
    font-size: 20px;
    line-height: 1.2;
  }
}

.gallery__item-genre {
  font-size: 12px;
  color: $accent-color-primary;
  line-height: 1.2;

  @include tablet {
    font-size: 12px;
    line-height: 1.2;
  }

  @include desktop {
    font-size: 20px;
    line-height: 1.2;
  }
}

.gallery__item-poster {
  height: 402;
  width: 280;
  object-fit: cover;
  border-radius: 5px;

  transition: transform $animation, box-shadow $animation;

  &:hover,
  &:focus {
    // box-shadow: 3px 3px 10px 5px rgb(248, 139, 5),
    //   0 0 4px $accent-color-primary inset;

    // transform: scale(1.02);

    box-shadow: rgb(255 107 8 / 42%) 0px 2px 4px 0px,
      rgb(255 107 8 / 62%) 0px 2px 16px 0px;
    transform: scale(1.01);
  }

  @include tablet {
    height: 455;
    width: 336;
  }

  @include desktop {
    height: 574;
    width: 395;
  }
}
.gallery__item-link {
  display: block;
  text-decoration: none;
  height: 100%;
}
