.header {
  min-width: 230px;
  height: 230px;
  padding-top: 40px;
  font-family: $font-primary;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  color: $light-color-primary;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  opacity: 0.9;

  background-image: url('../images/header-home-mob.jpg'), $bg-gradient;

  @include retina {
    background-image: url('../images/header-home-mob@2x.jpg'), $bg-gradient;
  }

  @include tablet {
    height: 216px;
    padding-top: 35px;
    background-image: url('../images/header-home-tab.jpg'), $bg-gradient;

    @include retina {
      background-image: url('../images/header-home-tab@2x.jpg'), $bg-gradient;
    }
  }

  @include desktop {
    background-image: url('../images/header-home-desk.jpg'), $bg-gradient;

    @include retina {
      background-image: url('../images/header-home-desk@2x.jpg'), $bg-gradient;
    }
  }

  &.library {
    background-image: url('../images/header-library-mob.jpg'), $bg-gradient;

    @include retina {
      background-image: url('../images/header-library-mob@2x.jpg'), $bg-gradient;
    }

    @include tablet {
      height: 216px;
      background-image: url('../images/header-library-tab.jpg'), $bg-gradient;

      @include retina {
        background-image: url('../images/header-library-tab@2x.jpg'),
          $bg-gradient;
      }
    }

    @include desktop {
      background-image: url('../images/header-library-desk.jpg'), $bg-gradient;

      @include retina {
        background-image: url('../images/header-library-desk@2x.jpg'),
          $bg-gradient;
      }
    }
  }
}

.navigation {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  @include tablet {
    margin-bottom: 0;
  }
}

.header__logo {
  display: flex;
  gap: 5%;
  align-items: center;
  text-decoration: none;
  transition: color 0.3s ease;

  &:focus,
  &:hover {
    color: $accent-color-primary;
    text-decoration: none;
  }
}

.logo__icon {
  display: inline-block;
  width: 35px;
  height: 35px;
  // fill: #ff001b;
  animation: film 4s infinite alternate;
}

@keyframes film {
  0.00% {
    fill: #ff001b;
  }
  16.67% {
    fill: #ff001b;
  }
  33.33% {
    fill: orange;
  }
  50.00% {
    fill: $text-color-accent;
  }
  66.67% {
    fill: orange;
  }
  83.33% {
    fill: #ff001b;
  }
  100.00% {
    fill: #ff001b;
  }
}

.logo__title {
  display: block;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.67;
  text-transform: uppercase;
  display: none;

  @include tablet {
    display: block;
  }
}

.header__menu-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  @include tablet {
    font-size: 14px;
  }
}

.header .header__menu-link {
  display: block;
  padding: 0 5px;
  text-align: center;
  text-decoration: initial;
  text-transform: uppercase;
  color: $bg-primary-light-color;
  transition: color 0.3s ease;

  &:not(.active):focus,
  &:not(.active):hover {
    color: $accent-color-primary;
    text-decoration: none;
  }

  &.active {
    text-decoration-line: underline;
    text-decoration-thickness: 3px;
    text-decoration-color: #ff001b;
    cursor: auto;
  }
}

.header .js-login {
  text-transform: capitalize;
  text-align: end;
}

.login-user {
  font-size: 14px;
}

.setting-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  @include tablet {
    margin: 0;
  }
}

.movie-search__form {
  display: flex;
  justify-content: space-between;
  margin: 0 auto 20px;
  width: 100%;
  border-bottom: 1px solid $light-color-primary;
  @include tablet {
    width: 50%;
  }
}

.search__form-input {
  width: 100%;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.14;
  color: $light-color-primary;
  background-color: inherit;
  outline: none;
  border: 0 none;
}

.search__button {
  width: 40px;
  background-color: inherit;
  border-radius: 50%;
  border: 1px;
  cursor: pointer;
  transition: transform 0.3s ease;

  &:hover,
  &:focus {
    transform: scale(1.3);
  }
}

.icon-search {
  width: 35px;
  height: 35px;
}

.header__filter-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.genre-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

//=======================

.theme-toggle {
  display: flex;
  align-items: center;
}

//dark theme*****
body {
  transition: background-color 0.3s ease;
  &.dark {
    background-color: #292c35;
  }
}


.checkbox {
  opacity: 0;
  position: absolute;
}

.label {
  display: flex;
  border-radius: 50px;
  align-items: center;
  justify-content: space-between;
  padding: 2px;
  position: relative;
  cursor: pointer;
  // background-color: #111;
  background-color: transparent;
  border: 1px solid $light-color-primary;
}

.ball {
  width: 20px;
  height: 20px;
  background-color: $accent-color-primary;
  position: absolute;
  top: 2px;
  left: 2px;
  border-radius: 50%;
  transition: transform 0.3s ease;
}

/*  target the elemenent after the label*/
.checkbox:checked + .label .ball {
  transform: translateX(19px);
}

.icon-sun {
  fill: yellow;
}

.icon-moon {
  fill: $light-color-primary;
}

